@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
}
.wrapper {
  width: 100%;
  height: 100%;
}
.login-page,
.home-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.input-wrapper {
  width: 350px;
}
.mantine-Checkbox-label {
  padding-left: 5px;
  color: #666;
}
